@import url("https://fonts.googleapis.com/css?family=Kaushan+Script");
@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url("https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css");
body {
  width: 100%;
  height: 100%;
  margin-right: -15px;
  padding: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

a:focus {
  outline: none; }

button::-moz-focus-inner {
  border: 0; }

hr {
  margin: 2vh; }

.form-control:valid {
  background-image: none !important; }

.form-control:invalid {
  background-image: none !important; }

.PrimaryButton {
  font-size: 1.2vw;
  border-color: #fed136 !important;
  background-color: #fed136 !important; }
  .PrimaryButton:hover {
    opacity: 0.7; }
  .PrimaryButton:active {
    border-color: #fed136 !important;
    background-color: #fed136 !important; }
  .PrimaryButton:focus {
    border-color: #fed136 !important;
    background-color: #fed136 !important;
    box-shadow: 0 0 0 0.2rem #fed136 !important; }

.PrimaryOutlineButton {
  font-size: 1.2vw;
  color: #fed136 !important;
  border-color: #fed136 !important; }
  .PrimaryOutlineButton:hover {
    background-color: #fed136 !important; }
  .PrimaryOutlineButton:active {
    background-color: #fed136 !important; }
  .PrimaryOutlineButton:focus {
    box-shadow: 0 0 0 0.2rem #fff !important; }

.SecondaryButton {
  font-size: 1.2vw;
  border-color: #31393a !important;
  color: #fff !important;
  background-color: #31393a !important; }
  .SecondaryButton:hover {
    opacity: 0.7; }
  .SecondaryButton:active {
    border-color: #dedada !important;
    color: #1f2525 !important;
    background-color: #dedada !important; }

.HeaderSeperator {
  width: 100%;
  height: 7vh;
  opacity: 0.3;
  background-color: #31393a;
  margin-top: 30px;
  margin-bottom: 30px; }

.HeaderBuffer {
  height: 60px; }

.ContentSeperatorTop {
  height: 0.5vw;
  margin-top: 5vh;
  background-color: #dedada; }

.ContentSeperatorBottom {
  height: 0.5vw;
  margin-bottom: 10vh;
  background-color: #dedada; }

.ContentContainer {
  width: 100%;
  background-color: whitesmoke;
  padding-bottom: 8vh;
  padding-left: 1.4vw;
  padding-right: 1.4vw; }

.TeamText {
  font-size: 1.1vw;
  color: #1f2525; }

.TeamIconLarge {
  font-size: 6vw;
  padding-top: 1vw;
  padding-bottom: 1vw;
  color: #31393a; }

.TeamIconSmall {
  font-size: 3vw;
  padding-top: 0.5vw;
  padding-bottom: 0.5vw;
  color: #31393a; }

.TeamCardHeader {
  font-size: 1.8vw;
  font-family: "Montserrat";
  background-color: #fed136 !important;
  color: #1f2525; }

.TeamCard {
  width: 16vw;
  height: 21vw;
  text-align: center;
  background-color: #fed136 !important;
  margin: 1vw;
  transition: transform 0.5s; }

.TeamCard:hover {
  transform: scale(1.15); }

.TeamBodyHeader {
  font-size: 2vw;
  font-family: "Montserrat"; }

.TeamBodyText {
  margin-top: 2vh;
  font-size: 1.3vw;
  font-family: "Arial"; }

.TeamBodyItemFrame {
  height: 35vh;
  border: 2px solid #31393a;
  border-radius: 5px;
  margin-top: 2vh;
  margin-bottom: 2vh; }

.TeamBodyItemContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%; }

.TeamBodyItemContainer {
  height: 100%;
  width: 100%; }

.TeamBodyItem {
  background-color: #dedada;
  border-radius: 5px;
  border: 1px solid #adadad;
  width: 96%;
  padding: 1.5vh;
  display: flex; }

.TeamBodyItemIconContainer {
  height: 6vh;
  width: 6vh; }

.TeamBodyItemIcon {
  border: 2px solid #31393a;
  border-radius: 50%;
  width: 100%;
  height: 100%; }

.TeamBodyItemTextPrimary {
  font-size: 1.4vw;
  font-family: "Montserrat";
  margin-left: 1vw;
  color: #1f2525;
  line-height: 6vh; }

.TeamBodyItemTextSecondary {
  font-size: 1vw;
  font-family: "Montserrat";
  margin-left: 1vw;
  color: #494949;
  line-height: 6vh; }
